import React, {Component} from 'react';
import './App.less';
import {share} from './utils/apis';
import reqwest from 'reqwest';

const getOpacity = (index, current) => {
    switch (Math.abs(Math.abs(index) - Math.abs(current))) {
        case 0:
            return 1;
        case 1:
            return .5;
        case 2:
            return .4;
        case 3:
            return .3;
        case 4:
            return .2;
        case 5:
            return .1;

        default:
            return 0.1;

    }
}

const getOffset = (el) => {
    let top = 0;
    let left = 0
    while (el) {
        top += el.offsetTop;
        left += el.offsetLeft;
        el = el.offsetParent;
    }
    return {
        left,
        top
    }
}

const getUrlParams = () => {
    const obj = {};
    const reg = /[^#?&]+=[^#?&]+/ig;
    const paramsArr = document.location.href.match(reg);
    if (paramsArr) {
        paramsArr.forEach(item => {
            const temp = item.split('=');
            obj[temp[0]] = temp[1];
        });
    }
    return obj;
}

class App extends Component {
    constructor() {
        super();
        this.state = {
            current: 0,
            audioSrc: '',
            x: 0,
            y: 0,
            data: {
                photo: [
                    require('./static/照片/崔乐乐.jpg'),
                    require('./static/照片/张向堂.jpg'),
                    require('./static/照片/曾紫华.jpg'),
                    require('./static/照片/范艳云.jpg'),
                    require('./static/照片/郭晶.JPG'),
                ],
                word: [
                    '郭晶  张向堂 曾紫华',
                    '范艳云 崔乐乐',
                    '黄金 于文波 滕海原',
                    '每一个人 都有属于他自己的时刻',
                    '回首过去 展望未来 接受祝福 期待明天的路',
                    '猪易祝你永远都一切顺心',
                    '祝你生日快乐，日子没烦恼!'
                ]
            },
            play: false,
            share: false
        };
        this.audio;
        this.avatar = '';
        this.listArr;
    }

    componentWillMount() {
        const resize = () => {
            const w = document.documentElement.offsetWidth;
            const h = document.documentElement.offsetHeight;
            if (w < 768) {
                document.documentElement.style.fontSize =  w / 750 * 100 + 'px';
            } else if (w >= 768) {
                document.documentElement.style.fontSize =  w / 1536 * 100 + 'px';
            }
            if (h > w) {
                this.setState({
                    x: w < 768 ? '.2rem' : '2.44rem',
                    y: w < 768 ? '3.94rem' : '6.98rem',
                });
            } else {
                    this.setState({
                    x: w < 768 ? '.2rem' : '2.44rem',
                    y: '4rem',
                });
            }
        };
        window.onresize = resize;
        resize();

    }

    async componentDidMount() {
        if (!this.list) {
            return;
        }
        this.listArr = this.list.querySelectorAll('li');
        var mySwiper = new Swiper('.swiper-container', {
            autoplay: true,//可选选项，自动滑动
            effect : 'fade',
            loop: true,
            fadeEffect: {
                crossFade: true,
            }
        })

        
        this.timer = setInterval(this.move, 5000)
    }

    move = () => {
        let {current, data} = this.state;
        if (current === data.word.length - 1) {
            this.list.style.setProperty('--moveT', '0px');
            this.setState({current: 0});
            return;
        } else {
            current = ++current;
            this.list.style.setProperty('--moveT',
        parseInt(this.list.style.getPropertyValue('--moveT'), 10) - this.listArr[current - 1].offsetHeight + 'px');
            this.setState({current});
        }
    }

    handleTouchMove = (e) => {
        e = e.targetTouches[0];
        const target = this.headPic;
        const htmlFontSize = parseInt(document.documentElement.style.fontSize, 10);
        let x = e.clientX - this.headPic.l;
        let y = e.clientY - this.headPic.t;
        let maxX = document.documentElement.clientWidth - (window.w < 768 ? htmlFontSize * 2.84 : htmlFontSize * 4.1) -  htmlFontSize * .2;
        let maxY = document.documentElement.clientHeight - target.offsetHeight - htmlFontSize * 1.6;
        x = Math.abs(x) >= maxX ? maxX : x <= htmlFontSize * .2 ? htmlFontSize * .2 : x;
        y = y >= maxY ? maxY : y <= htmlFontSize * .16 ? htmlFontSize * .16 : y;
        this.setState({
            x: x + 'px',
            y: y + 'px'
        });
    }
    handleTouchEnd = () => {
        document.body.removeEventListener('touchmove', this.handleTouchMove);
        document.body.removeEventListener('touchend', this.handleTouchEnd);
    }

    handleStart(e) {
        e.persist();
        e.preventDefault();
        e = e.targetTouches[0];
        const target = this.headPic;
        this.headPic.t = e.clientY - getOffset(target).top;
        this.headPic.l = e.clientX - target.offsetLeft;
        document.body.addEventListener('touchmove', this.handleTouchMove);
        document.body.addEventListener('touchend', this.handleTouchEnd);
    }

    handleEnded = () => {
        let {current, data} = this.state;
        current = current === data.book_info.bookpages.length - 1 ? 0 : ++current;
        if (current === 0) {
            this.list.style.setProperty('--moveT', 0 + 'px');
        } else {
            this.list.style.setProperty('--moveT',
                parseInt(this.list.style.getPropertyValue('--moveT'), 10) - this.listArr[current - 1].offsetHeight + 'px');
        }

        this.setState({
            current
        });
    }
    handlePlaying = () => {
        this.setState({
            play: true
        });
    }

    handlePre = () => {
        clearInterval(this.timer);
        let current = this.state.current;
        if (current === 0) {
            return;
        } else {
            current = --current;
        }
        this.list.style.setProperty('--moveT',
            parseInt(this.list.style.getPropertyValue('--moveT'), 10) + this.listArr[current].offsetHeight + 'px');

        this.setState({
            current,
            // play: false
        });
        if (!this.audio.paused) {
            this.timer = setInterval(this.move, 5000)
        }
    }

    handleNext = () => {
        clearInterval(this.timer);
        let {current, data} = this.state;
        if (current === data.word.length - 1) {
            return;
        } else {
            current = ++current;
        }
        this.list.style.setProperty('--moveT',
            parseInt(this.list.style.getPropertyValue('--moveT'), 10) - this.listArr[current - 1].offsetHeight + 'px');

        this.setState({
            current,
            // play: false
        });
        if (!this.audio.paused) {
            this.timer = setInterval(this.move, 5000)
        }
    }

    handlePause = () => {
        if (!this.audio) {
            return;
        }
        this.setState({
            play: this.audio.paused
        });
        this.audio.paused ? (this.audio.play(), this.timer = setInterval(this.move, 5000)) : (this.audio.pause(), clearInterval(this.timer));
    }

    handleShare = (share) => {
        this.setState({
            share
        });
    }

    render() {
        const {data, current, x, y, play, share} = this.state;
        const info = data['student_info'] ? data['student_info'] : data['teacher_info'];
        const flag = document.documentElement.clientWidth > 700;
        return (
            flag ? <div>请使用手机端打开页面</div>     :
            <React.Fragment>
                    <div className="bg" style={{"backgroundImage": `url(${data['photo'][3]})`}}></div>
                    <span>
                        <span className='cake bg1'>🎂</span>
                        <span className='cake bg2'>🎂</span>
                        <span className='cake bg3'>🎂</span>
                        <span className='cake bg4'>🎂</span>
                        <span className='cake bg5'>🎂</span>
                    </span>
                    <div className="main">
                        <header className="cover swiper-container" >
                            <div className="swiper-wrapper">
                                {data.photo.map((item, index) => <div key={index} className="swiper-slide"><img src={item} alt="封面"/></div>)}
                                
                            </div>
                            <div className="swiper-pagination"></div>
                            
                        </header>
                        {/* <section 
                            onTouchStart={this.handleStart.bind(this)}
                            style={{left: x, top: y}} 
                            className={`head ${data['book_info'].pic ? 'opacity' : ''}`}
                            ref={(headPic) => {
                                this.headPic = headPic
                            }}
                        >
                            <img src={this.avatar} alt="头像"/>
                            <div className="student-name">{info.en_name}</div>
                        </section>*/}
                        <section className="content">
                            <ul ref={(list) => {
                                this.list = list
                            }} style={{'--moveT': '0px'}}>
                                {data.word.map((item, index) => {
                                    return <li key={index} style={{opacity: getOpacity(index, current)}}>
                                        <span>{item}</span>
                                    </li>
                                })}
                            </ul>
                        </section> 
                        <footer className="footer">
                            <div className="logo">
                                <a href="http://www.zhue.com.cn">
                                    <img src={require('./static/images/lllllll.png')} alt="logo"/>
                                </a>
                            </div>
                            <div className="operation">
                                <div onClick={this.handlePause}>
                                    <img
                                        src={play ? require('./static/images/music_btn_play.png') : require('./static/images/music_btn_stop.png')}
                                        alt="logo"/>
                                </div>
                                <div onClick={this.handlePre}>
                                    <img src={require('./static/images/music_btn_left.png')} alt="logo"/>
                                </div>

                                <div onClick={this.handleNext}>
                                    <img src={require('./static/images/music_btn_right.png')} alt="logo"/>
                                </div>
                            </div>
                            <div className="share" onClick={this.handleShare.bind(this, !share)}>
                                <img src={require('./static/images/music_btn_share.png')} alt="logo"/>
                            </div>
                        </footer>
                        <audio
                                autoPlay
                                preload='auto'
                                ref={(audio) => {
                                    this.audio = audio
                                }}
                                // src={require(`${data[current]['src']}`)}
                                src={require('./static/Mongol800-小さな恋のうた (小小恋歌).mp3')}
                                onEnded={this.handleEnded}
                                onPlaying={this.handlePlaying}
                            >
                        </audio>

                    </div>
                    <section className="shareMask" style={{display: share ? 'block' : 'none'}}
                             onClick={this.handleShare.bind(this, false)}>
                        <div>
                            <img src={require('./static/images/share.png')} alt='fenxiang'/>
                        </div>
                    </section>
                </React.Fragment>
        );
    }
}

export default App; 



